	module.exports = {"data":{"challengeNode":{"title":"Use the align-self Property","description":["flex 子元素的最后一个属性是<code>align-self</code>。这个属性允许你调整每个项目自己的对齐方式，而不是一次性设置全部项目。因为<code>float</code>、<code>clear</code>和<code>vertical-align</code>等调整使用的属性都不能应用在 flex 子元素，所以这个属性显得十分有用。","<code>align-self</code>的允许值与<code>align-items</code>一样，并且它会覆盖<code>align-items</code>的值。","<hr>","在<code>#box-1</code>和<code>#box-2</code>添加 CSS 属性<code>align-self</code>。<code>#box-1</code>设为 center，<code>#box-2</code>设为 flex-end。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/css-flexbox/use-the-align-self-property","blockName":"CSS Flexbox","tests":[{"text":"<code>#box-1</code>元素应有<code>align-self</code>属性，其值应为 center。","testString":"assert($('#box-1').css('align-self') == 'center', '<code>#box-1</code>元素应有<code>align-self</code>属性，其值应为 center。');"},{"text":"<code>#box-2</code>元素应有<code>align-self</code>属性，其值应为 flex-end。","testString":"assert($('#box-2').css('align-self') == 'flex-end', '<code>#box-2</code>元素应有<code>align-self</code>属性，其值应为 flex-end。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  #box-container {\n    display: flex;\n    height: 500px;\n  }\n  #box-1 {\n    background-color: dodgerblue;\n    \n    height: 200px;\n    width: 200px;\n  }\n\n  #box-2 {\n    background-color: orangered;\n    \n    height: 200px;\n    width: 200px;\n  }\n</style>\n\n<div id=\"box-container\">\n  <div id=\"box-1\"></div>\n  <div id=\"box-2\"></div>\n</div>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"/responsive-web-design/css-grid","template":null,"required":[],"nextChallengePath":"/responsive-web-design/css-grid/create-your-first-css-grid","id":"587d78af367417b2b2512b00"},"slug":"/responsive-web-design/css-flexbox/use-the-align-self-property"}}

